'use client';
import { Card } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { useForm } from 'react-hook-form';
import { zodResolver } from '@hookform/resolvers/zod';
import { bookingCheckInSchema, BookingCheckInForm } from '@/validation/booking-checkin-schema';
import { Booking } from '@/types/bookings';
import { toast } from 'sonner';

export function BookingCheckInCard({ booking, onSuccess }: { booking: Booking; onSuccess?: () => void }) {
  const hobbsStart = booking.hobbs_start ?? 0;
  const tachStart = booking.tach_start ?? 0;

  const {
    register,
    handleSubmit,
    formState: { errors, isSubmitting },
  } = useForm<BookingCheckInForm>({
    resolver: zodResolver(bookingCheckInSchema),
    defaultValues: {
      hobbs_start: hobbsStart,
      tach_start: tachStart,
      hobbs_end: booking.hobbs_end ?? hobbsStart,
      tach_end: booking.tach_end ?? tachStart,
    },
  });

  const onSubmit = async (data: BookingCheckInForm) => {
    const res = await fetch(`/api/bookings/${booking.id}/checkin`, {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify({ hobbs_end: data.hobbs_end, tach_end: data.tach_end }),
    });
    if (res.ok) {
      toast.success('Check-in successful!');
      onSuccess?.();
    } else {
      const result = await res.json();
      toast.error(result.error || 'Check-in failed');
    }
  };

  return (
    <Card className="p-6 w-full max-w-xl mx-auto">
      <h2 className="text-2xl font-bold mb-4">Aircraft Check-In</h2>
      <form onSubmit={handleSubmit(onSubmit)} className="flex flex-col gap-6">
        <div className="grid grid-cols-2 gap-6">
          <div>
            <label className="block text-sm font-medium mb-1">Start Hobbs</label>
            <Input value={hobbsStart} readOnly className="bg-slate-100" />
          </div>
          <div>
            <label className="block text-sm font-medium mb-1">End Hobbs</label>
            <Input
              type="number"
              step="0.01"
              {...register('hobbs_end', { valueAsNumber: true })}
              min={hobbsStart}
              required
            />
            {errors.hobbs_end && (
              <span className="text-red-500 text-xs">{errors.hobbs_end.message}</span>
            )}
          </div>
          <div>
            <label className="block text-sm font-medium mb-1">Start Tacho</label>
            <Input value={tachStart} readOnly className="bg-slate-100" />
          </div>
          <div>
            <label className="block text-sm font-medium mb-1">End Tacho</label>
            <Input
              type="number"
              step="0.01"
              {...register('tach_end', { valueAsNumber: true })}
              min={tachStart}
              required
            />
            {errors.tach_end && (
              <span className="text-red-500 text-xs">{errors.tach_end.message}</span>
            )}
          </div>
        </div>
        <input type="hidden" {...register('hobbs_start')} value={hobbsStart} />
        <input type="hidden" {...register('tach_start')} value={tachStart} />
        <Button type="submit" disabled={isSubmitting} className="w-full mt-4">
          Complete Check-In
        </Button>
      </form>
    </Card>
  );
} 